<template>
  <div id="app">
    <mq-layout :mq="['mobile', 'tablet']">
      <MobNavigation />
    </mq-layout>
    <mq-layout mq="laptop+">
    <div class="nav-wrap">
      <a href="/" rel="noopener">
        <img :src="require('@/assets/logo.png')" width="125px" height="100px" alt="Website logo" />
      </a>
      <Navigation />
    </div>
    </mq-layout>
    <router-view />
    <Footer />
  </div>
</template>

<script>
import Navigation from '@/components/Navigation.vue'
import MobNavigation from '@/components/MobNavigation.vue'
const Footer = () => import(/* webpackChunkName: "footer", webpackPrefetch: true */ '@/components/Footer.vue')

export default {
  components: {
    Navigation,
    MobNavigation,
    Footer
  },

  created () {
    if (this.$workbox) {
      this.$workbox.addEventListener('waiting', () => {
        this.showUpgradeUI = true
      })
    }
  },

  methods: {
    async accept () {
      this.showUpgradeUI = false
      await this.$workbox.messageSW({ type: 'SKIP_WAITING' })
    }
  }
}
</script>

