<template>
  <div id="page-title">
    <h1 v-if="title.length">
      <span v-html="title" />
    </h1>
    <img :src="require(`@/assets/${imageName}`)" width="100%" height="100%" alt="Image representing page title" />
    <a @click="scrollDown()">
      <span></span>
      Scroll
    </a>
  </div>
</template>

<script>

export default {
  name: 'PageTitle',

  props: {
    imageName: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    }
  },

  methods: {
    scrollDown () {
      document.querySelector('section').scrollIntoView({
        behavior: 'smooth',
        block: 'start',
        inline: 'nearest'
      })
    }
  }
}
</script>

