<template>
  <div id="mob-menu">
    <div class="mob-bar" :class="[$mq, { 'scrolled-bar' : !top }]">
      <div class="logo-container">
        <a href="/" rel="noopener">
          <img :src="require('@/assets/logo.png')" :width="`${top ? 100 : 80}px`" :height="`${top ? 80 : 65}px`" alt="Website logo" />
        </a>
      </div>
      <div
        id="menu-icon"
        :class="{ 'open' : open }"
        @click="menuClick()"
      >
        <span v-for="span in 4" :key="span"></span>
      </div>
    </div>
    <nav class='nav' :class="[open ? 'open' : 'close', { 'scrolled-nav' : !top }]">
      <router-link @click.native="open = false" to="/">Home</router-link>
      <router-link @click.native="open = false" to="/about">About</router-link>
      <router-link @click.native="open = false" to="/resume">Resume</router-link>
      <router-link @click.native="open = false" to="/contact">Contact</router-link>
    </nav>
  </div>
</template>

<script>
export default {
  name: 'MobNavigation',

  data () {
    return {
      open: false,
      top: true
    }
  },

  mounted () {
    this.scroll()
  },

  methods: {
    menuClick () {
      this.open = !this.open
    },
    scroll () {
      window.onscroll = () => {
        this.top = Math.max(window.pageYOffset, document.documentElement.scrollTop, document.body.scrollTop) === 0
      }
    }
  }

}
</script>

